import React from "react";
import "./Production.css";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";

function Production() {
  // *******************************************************
  const [selectedFile, setSelectedFile] = useState(null);

  // **************************************************** */
  const [showNewOrder, setshowNewOrder] = useState(false);
  const [status, setStatus] = useState("");
  const navigate = useNavigate();
  const [formValues, setFormValues] = useState({
    nameOrder: "",
    category: " ",
    descOrder: "",
    quantity: "",
    materialOrder: " ",
    sample: " ",
    delivery: "",
    deliveryType: "-",
    orderDeadline: "",
    payment: "",
    moreServices: "",
    comments: "",
    // mediaProduct: "",
    users_permissions_users: "",
  });

  const onChangeSelected = (e) => {
    setFormValues((formValues) => {
      return {
        ...formValues,

        [e.target.name]: e.target.value,
        users_permissions_users: localStorage.getItem("id"),
      };
    });
  };

  const uploadFiles = (e) => {
    setFormValues((formValues) => {
      return {
        ...formValues,
        mediaProduct: URL.createObjectURL(e.target.files[0]),
      };
    });
  };

  const onClickBackNewOrder = () => {
    setshowNewOrder(true);
    navigate("/new-order");
  };

  const onFinish = async (e) => {
    e.preventDefault();

    // *****************************************
    const formData = new FormData();
    formData.append("files", selectedFile);

    const uploadResponse = await axios.post(
      "http://localhost:1337/api/upload",
      formData,
      {
        headers: {
          "Content-Type": "multipart/form-data",
        },
        params: {
          ref: "production",
          refId: localStorage.getItem("id"), //     
          field: "mediaProduct", //    ,  
        },
      }
    );

    const fileId = uploadResponse.data[0].id;
    console.log(fileId);

    // *****************************************
    setFormValues((formValues) => {
      return {
        ...formValues,
        mediaProduction: fileId,
      };
    });

    // addProductionProduct(formValues);

    const createResponse = await axios.post(
      "http://localhost:1337/api/productions",
      {
        data: {
          ...formValues,
          mediaProduct: fileId,
        },
      }
    );
    console.log(createResponse.data);

    const token = "6059462033:AAHMTNU6CakxUuMjoaiayqgkAN1R-cyxQ-A";
    const chat_id = "-1001950653999"; //    ,https://api.telegram.org/botXXXXXXXXXXXXXXXXXXXXXXX/getUpdates,
    // , XXXXXXXXXXXXXXXXXXXXXXX -   ,  
    const button = {
      text: "test btn",
      url: `https://t.me/${localStorage.getItem("tg")}`,
    };

    //         
    const inlineKeyboard = {
      inline_keyboard: [[button]],
    };

    //     JSON 
    const keyboardJSON = JSON.stringify(inlineKeyboard);

    //    API     
    const url =
      "https://api.telegram.org/bot" +
      token +
      "/sendMessage?chat_id=" +
      chat_id +
      "&parse_mode=html&text=" +
      encodeURIComponent(
        "<b><i> :</i></b>  " +
          formValues.nameOrder +
          "\n <b><i>:</i></b>  " +
          formValues.category +
          "\n<b><i> :</i></b>  " +
          formValues.descOrder +
          // "\n<b><i>:</i></b>  " +
          // formValues.quantity +
          // "\n<b><i>  :</i></b>  " +
          // formValues.materialOrder +
          // "\n<b><i>:</i></b>  " +
          // formValues.sample +
          // "\n<b><i>:</i></b>  " +
          // formValues.delivery +
          // "\n<b><i> :</i></b>  " +
          // formValues.deliveryType +
          // "\n<b><i>  :</i></b>  " +
          // formValues.orderDeadline +
          // "\n<b><i> :</i></b>  " +
          // formValues.payment +
          // "\n<b><i> :</i></b>  " +
          // formValues.moreServices +
          // "\n<b><i>  :</i></b>  " +
          // formValues.comments +
          // "\n<b><i> :</i></b>  " +
          // formValues.mediaProduct +
          "\n"
      ) +
      "&reply_markup=" +
      encodeURIComponent(keyboardJSON);
    try {
      const response = await fetch(url);
      const data = await response.json();
      if (data.ok) {
        setStatus("  ");
      } else {
        setStatus("");
      }
    } catch (error) {
      console.error(error);
      setStatus("");
    }
  };

  return (
    <>
      <div className="production-container">
        <div className="production-content">
          <h3>:</h3>
          <form className="form-production">
            <label>
               :
              <input
                id="production-inputs"
                className="production-nameOrder"
                placeholder=":  "
                name="nameOrder"
                onChange={onChangeSelected}
              />
            </label>
            <label className="production-lable-block">
               :
              <select
                id="production-inputs"
                className="production-category"
                name="category"
                onChange={onChangeSelected}
              >
                <option value=" "> </option>
                <option value=" "> </option>
                <option value=" "> </option>
                <option value=""></option>
              </select>
            </label>
            <label className="production-lable-block">
               :
              <textarea
                cols={10}
                rows={5}
                id="production-inputs"
                className="production-descripton"
                placeholder=":  /"
                name="descOrder"
                onChange={onChangeSelected}
              />
            </label>
            <label className="production-lable-block">
              :
              <input
                type="number"
                id="production-inputs"
                className="production-descripton"
                name="quantity"
                onChange={onChangeSelected}
              />
            </label>
            <label className="production-lable-block">
                :
              <select
                id="production-inputs"
                className="production-material"
                name="materialOrder"
                onChange={onChangeSelected}
              >
                <option value=" ">
                   
                </option>
                <option value=" ">
                   
                </option>
                <option value=" "> </option>
              </select>
            </label>
            <label className="production-lable-block">
              :
              <select
                id="production-inputs"
                className="production-sample"
                name="sample"
                onChange={onChangeSelected}
              >
                <option value=" ">
                   
                </option>
                <option value=" "> </option>
              </select>
            </label>
            {/* ****************************************************************** */}
            <label className="production-lable-block">
              :
              <select
                id="production-inputs"
                className="production-delivery"
                name="delivery"
                onChange={onChangeSelected}
              >
                <option value=""></option>
                <option value=""></option>
                <option value=" "> </option>
              </select>
            </label>
            <label className="production-lable-block">
               :
              <select
                id="production-inputs"
                className="production-deliveryType"
                name="deliveryType"
                onChange={onChangeSelected}
              >
                <option value="-">-</option>
                <option value=" "> </option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </label>

            <label className="production-lable-block">
                :
              <input
                type="date"
                id="production-inputs"
                className="production-orderDeadline"
                name="orderDeadline"
                onChange={onChangeSelected}
              />
            </label>
            <label className="production-lable-block">
               :
              <input
                type="text"
                id="production-inputs"
                className="production-payment"
                name="payment"
                onChange={onChangeSelected}
                placeholder=": ,   .."
              />
            </label>

            <label className="production-lable-block">
               :
              <input
                type="text"
                id="production-inputs"
                className="production-moreServices"
                name="moreServices"
                onChange={onChangeSelected}
                placeholder=": ,   .."
              />
            </label>

            <label className="production-lable-block">
                :
              <textarea
                id="production-inputs"
                className="production-comments"
                name="comments"
                onChange={onChangeSelected}
              />
            </label>

            {/* ****************************************************************** */}

            <label className="production-lable-block">
               .   :
              <input
                type="file"
                id="production-inputs"
                name="mediaProduct"
                onChange={(e) => {
                  setSelectedFile(e.target.files[0]);
                }}
                multiple
              />
              {/* <SendImgToStrapi /> */}
            </label>

            {/* ******************************************************************* */}

            <button
              type="submit"
              className="btn-production-send-form"
              style={{ marginBottom: "10px" }}
              onClick={onFinish}
            >
                
            </button>
          </form>
          <button
            type="primary"
            className="btn-production-back"
            onClick={onClickBackNewOrder}
          >
            
          </button>
          {showNewOrder && navigate("/new-order")}
        </div>
      </div>
    </>
  );
}

export default Production;
